.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
button{font-family:Segoe UI,Tahoma,Geneva,Verdana,sans-serif}
button{padding:16px;font-size:16px;background-color:#000;color:#fff;border-radius:8px;cursor:pointer}
h3,h4{margin-bottom:0}nav{border-bottom:4px solid #000;text-align:center;position:fixed;top:0;width:100%;background:#fff}
nav ul{list-style-type:none;margin:0;padding:0}nav li{display:inline-block;padding-top:16px;padding-bottom:16px}
nav a{text-decoration:none;color:#000;font-weight:700;padding:16px 32px}
nav a:hover{background:#000;color:#fff}
#page-body{max-width:700px;margin:auto;padding-top:64px}
.article-list-item{text-decoration:none;color:#000;padding:16px 0 0}
.article-list-item p{padding-bottom:32px;border-bottom:2px solid #000}
#add-comment-form{border-bottom:2px solid #000;padding:16px}
#add-comment-form h3{margin-bottom:16px}
#add-comment-form input,textarea{display:block;width:300px;padding:8px;border-radius:8px;font-family:Segoe UI,Tahoma,Geneva,Verdana,sans-serif;font-size:18px;border:2px solid #000;margin-bottom:16px}
#add-comment-form button{width:320px}
#upvotes-section button,p{display:inline-block;margin-right:16px}
.comment{border-bottom:2px solid #000;padding:16px}
